.success {
  font-family: system-ui, sans-serif;
  font-size: 2rem;
  background-color: lightgray;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  border-style: solid;
  color: green;
}
.failure {
  font-family: system-ui, sans-serif;
  font-size: 2rem;
  background-color: lightgray;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  border-style: solid;
  color: red;
}
